@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
*{
    box-sizing: border-box;
    margin: 0;
    font-family: 'Lato', sans-serif;
    color: rgb(92, 92, 92);
}

h1{
    text-align: center;
    margin: -90px 0 20px 0;
    font-size: 50px;
    color: white;
    text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

body{
    background: linear-gradient(20deg, rgb(16, 119, 76), rgb(255, 196, 0));
    background-repeat: no-repeat;
    height: 100vh;
    align-items: center;
    display: flex;
    justify-content: center;
}

.infoDiv {
    width: 500px;
    padding: 20px 30px;
    background-color: rgb(241, 241, 241);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 7px;
}

form{
    display: flex;
    justify-content: space-between;
}

button:hover{
    background-color: rgb(226, 226, 226);
    color: rgb(119, 119, 119);
}

.generalInfo:nth-child(1){
    padding: 0 10px;
}

.generalInfo > div:nth-child(1){
    display: flex;
    justify-content: space-between;
}

.generalInfo > div{
    display: flex;
    margin: 10px 0 0 0;
}

.generalInfo > div:nth-child(1) > div:nth-child(1), .generalInfo > div:nth-child(1) > div:nth-child(2){
    display: flex;
}

.generalInfo > div:nth-child(1) > div:nth-child(1) > p:nth-child(2), .generalInfo> div:nth-child(3) > p:nth-child(3){
    color: rgb(223, 171, 0);
}

p{
    padding: 0 0 0 10px;
    font-weight: 600;
}

.generalInfo > :nth-child(2) > :nth-child(2), .generalInfo > :nth-child(3) > :nth-child(5){
    color: rgb(39, 196, 73);
}

.generalInfo > div:nth-child(2) > :nth-child(4), .generalInfo > div:nth-child(3) > :nth-child(7){
    color: rgb(219, 51, 51);
}

@media (max-width: 512px){
    .infoDiv{
        width: 400px;
    }
    *{
        font-size: 14px;
    }
    h1{
        font-size: 40px;
    }
}

@media (max-width: 455px){
    .infoDiv{
        width: 350px;
    }
    *{
        font-size: 13px;
    }
    h1{
        font-size: 35px;
    }
}

@media (max-width: 430px){
    .infoDiv{
        width: 330px;
    }
    *{
        font-size: 12px;
    }
    h1{
        font-size: 30px;
    }
}

@media (max-width: 408px){
    *{
        font-size: 11px;
    }
}

@media (max-width: 385px){
    *{
        font-size: 10px;
    }
}

@media (max-width: 362px){
    *{
        font-size: 9px;
    }
}

@media (max-width: 340px){
    *{
        font-size: 8px;
    }
}